import React, { useState, useRef, useEffect } from "react";

export const Seconder = ({ setSeconderStarted, setValueTextArea, gameWon, setGameWon, setTimeWiner, allGames, setAllGames, user }) => {
    const [milisecond, setMiliecond] = useState(0);
    const [second, setSecond] = useState(0);
    const [minute, setMinute] = useState(0);
    const intervalRef = useRef(null);

    const secondStart = () => {
        if (intervalRef.current) {
            clearInterval(intervalRef.current);
        }

        intervalRef.current = setInterval(() => {
            setMiliecond(prevMilisecond => {
                if (prevMilisecond >= 99) {
                    setSecond(prevSecond => {
                        if (prevSecond >= 59) {
                            setMinute(prevMinute => prevMinute + 1);
                            return 0;
                        } else {
                            return prevSecond + 1;
                        }
                    });
                    return 0;
                } else {
                    return prevMilisecond + 1;
                }
            });
        }, 10);
        setValueTextArea("")
        setSeconderStarted(true);
        setGameWon(false)
    };
    useEffect(() => {
        if (gameWon) {
            let time = `${minute.toString().padStart(2, '0')}:${second.toString().padStart(2, '0')}:${milisecond.toString().padStart(2, '0')}`
            let name = user.name
            setTimeWiner(time)
            setAllGames((prevAllGames) => {
                return {
                    ...prevAllGames,
                    names: prevAllGames.names ? [...prevAllGames.names, name] : [name],
                    times: prevAllGames.times ? [...prevAllGames.times, time] : [time],
                };
            });
            console.log("times:", allGames.times);

            clearInterval(intervalRef.current);
        }
    }, [gameWon, minute, second, milisecond, setTimeWiner, allGames, setAllGames, user]); //  "name"  
    useEffect(() => {
        return () => {
            //  ,   
            if (intervalRef.current) {
                clearInterval(intervalRef.current);
            }
        };
    }, [])
    return (
        <div>
            <div>{`${minute.toString().padStart(2, '0')}:${second.toString().padStart(2, '0')}:${milisecond.toString().padStart(2, '0')}`}</div>
            <button onClick={secondStart}>start</button>
        </div>
    );
};
