import React, { useState } from "react";
import { Formik, Field, Form } from "formik"
import { Navigate } from "react-router-dom";
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';


function validateUsername(value) {
    let error;
    if (value === '') {
        error = "can't play without name";
    }
    return error;
}
const currencies = [
    {
        value: 'black',
        label: 'black',
    },
    {
        value: 'purple',
        label: 'purple',
    },
    {
        value: 'green',
        label: 'green',
    }
];
export const LocalLogin = ({ setUser, user, allGames, setAllGames }) => {
    const [acceptSubmit, setAcceptSubmit] = useState(false)
    const CustomInputComponent = ({ field, ...props }) => (
        <TextField type="text" {...field} {...props} id="outlined-basic" label="User Name" variant="outlined" />
    );
    const CustomSelectComponent = ({ field, ...props }) => (
        <TextField
            {...props}
            {...field}
            id="outlined-select-currency"
            select
            label="Color"
            defaultValue="black"
            helperText="You can change color text"
        >
            {currencies.map((option) => (
                <MenuItem style={{ color: option.value }} key={option.value} value={option.value}>
                    {option.label}
                </MenuItem>
            ))}
        </TextField>
    );
    return (
        <div className="login-box">
            {acceptSubmit && <Navigate to="/game" replace={true} />}
            <Formik
                initialValues={{ name: "", color: "" }}
                onSubmit={(values) => {
                    const name = values.name;
                    setUser({ name: name, color: values.color });
                    if (allGames.names.length > 0) {
                        setAcceptSubmit(true);
                    }
                    console.log("names:", allGames.names);
                }}

            >
                {({ errors, touched }) => (
                    <Form className="login-form">
                        <div><h1 className="login-form__title">Get Start</h1></div>
                        <div className="login-form__box">
                            <div>
                                <div style={{ width: "215px" }}>
                                    <div><Field style={{ width: "100%" }} component={CustomInputComponent} type="text" name="name" validate={validateUsername} /></div>
                                    {errors.name && touched.name && <Alert style={{ marginTop: "10px", width: "190px" }} severity="error">{errors.name}</Alert>}
                                </div>
                            </div>
                            <div>
                                <div>
                                    <Field component={CustomSelectComponent} name="color" id="" />
                                </div>
                            </div>
                        </div>
                        <Button type="submit" variant="contained">
                            Start
                        </Button>
                        {user.name !== "" && <Navigate to="/game" />}
                    </Form>
                )}
            </Formik>
        </div >
    )
}