import React, { useState } from "react";
import { Navigate } from "react-router-dom";

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ThreeSixtyIcon from '@mui/icons-material/ThreeSixty';
import AlarmIcon from '@mui/icons-material/Alarm';
import { Button } from "@mui/material";
import SvgIcon from '@mui/material/SvgIcon';

function SvgIconChildren() {
    return (
        <SvgIcon>
            {/* credit: plus icon from https://heroicons.com/ */}
            <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
            >
                <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 12a7.5 7.5 0 0015 0m-15 0a7.5 7.5 0 1115 0m-15 0H3m16.5 0H21m-1.5 0H12m-8.457 3.077l1.41-.513m14.095-5.13l1.41-.513M5.106 17.785l1.15-.964m11.49-9.642l1.149-.964M7.501 19.795l.75-1.3m7.5-12.99l.75-1.3m-6.063 16.658l.26-1.477m2.605-14.772l.26-1.477m0 17.726l-.26-1.477M10.698 4.614l-.26-1.477M16.5 19.794l-.75-1.299M7.5 4.205L12 12m6.894 5.785l-1.149-.964M6.256 7.178l-1.15-.964m15.352 8.864l-1.41-.513M4.954 9.435l-1.41-.514M12.002 12l-3.75 6.495"
                />
            </svg>
        </SvgIcon>
    );
}
export const PlayerTab = ({ timeWiner, allGames, user, setTimeWiner, setUser }) => {

    const [restart, setRestart] = useState(false);
    const [sortAscending, setSortAscending] = useState(true);
    const [userMenu, setUserMenu] = useState(false)
    // const sortAllGames = (sortByField, sortAscending) => {

    //     let sortedArray = [];

    //     if (Array.isArray(allGames.times)) {
    //         sortedArray = allGames.times.slice().sort((a, b) => {
    //             //      
    //             const timeA = timeStringToMilliseconds(a);
    //             const timeB = timeStringToMilliseconds(b);

    //             return timeA - timeB;
    //         });
    //     }


    //     //      ,  
    //     if (!sortAscending) {
    //         sortedArray.reverse();
    //     }

    //     return sortedArray;
    // };

    // const timeStringToMilliseconds = (time) => {
    //     const [hours, minutes, seconds] = time.split(":").map(Number);
    //     return hours * 3600000 + minutes * 60000 + seconds * 1000;
    // };
    const uniqueUsers = [...new Set(allGames.names)];

    return (
        <div >
            {user.name === "" && <Navigate to="/login" />}
            {restart && <Navigate to="/game" />}
            {timeWiner === null ? (
                <div>Loading...</div>
            ) : (
                <div>
                    <div onClick={() => setUserMenu(false)} className={userMenu ? "user-menu__bg active" : "user-menu__bg"}>
                        <div className={userMenu ? "user-menu active" : "user-menu"}>
                            {Array.isArray(allGames.names) && uniqueUsers.map((user, index) => (
                                <Button onClick={() => {
                                    setUser({ name: user })
                                    setUserMenu(false)
                                }} color="info" style={{ textTransform: "initial" }} key={index}>{user}</Button>
                            ))}
                        </div>
                    </div>
                    <div className="tab-box">
                        <div>
                            <h3 style={{ paddingLeft: "15px" }}>Last game<Button onClick={() => setUserMenu(!userMenu)} color="info" style={{ textTransform: "initial" }}>{user.name}<SvgIconChildren /></Button></h3>
                            <div style={{ paddingLeft: "20px", marginBottom: "15px" }}>{timeWiner}</div>
                            <Button style={{ marginRight: "10px" }} variant="contained" color="success" size="small"
                                onClick={() => {
                                    setUser({ name: "" })
                                    setTimeWiner(null)
                                    setSortAscending(false)
                                }}>Add user
                            </Button>
                            <Button
                                onClick={() => {
                                    setRestart(true)
                                    setTimeWiner(null)
                                    setSortAscending(false)
                                }}
                                variant="outlined" size="small">
                                Restart
                                <ThreeSixtyIcon />
                            </Button>
                        </div>
                        <TableContainer component={Paper} style={{ minWidth: "300px" }}>
                            <Table aria-label="simple table" >
                                <TableHead >
                                    <TableRow>
                                        <TableCell>Name</TableCell>
                                        <TableCell className={sortAscending ? "time-box active" : "time-box"} align="right" onClick={() => setSortAscending(!sortAscending)}>
                                            Time <AlarmIcon className="time-box__icon" />
                                        </TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {Array.isArray(allGames.names) && allGames.names.map((name, index) => (
                                        <TableRow
                                            key={index}
                                            sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                        >
                                            <TableCell component="th" scope="row">
                                                {name}
                                            </TableCell>
                                            <TableCell align="right">{allGames.times[index]}</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>

                    </div>
                </div>
            )
            }
        </div >
    );
};
